.swiper-wrapper {
    height: auto !important;
}

/* In your CSS */
.swiper-backface-hidden .swiper-slide {
    backface-visibility: visible !important;
}
:where(.wp-site-blocks *:focus) {
    outline-width: 0 !important;
}


  .fb-marquee-carousel-image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%;
    }
  }

  .fb-marquee-carousel-content {
    position: relative;
    overflow: hidden;
    font-size: 14px;
  }
  
  .fb-marquee-carousel-text,
  .fb-marquee-carousel-hover-text {
    transition: transform 300ms cubic-bezier(0.16, 1, 0.3, 1);
  }
  
  .fb-marquee-carousel-text,
  .fb-marquee-carousel-hover-text {
    transform: translateY(0%);
  }
  
  .fb-marquee-carousel-hover-text {
    position: absolute;
  }
  
  .fb-marquee-carousel-content .fb-marquee-carousel-hover-text {
    bottom: -100%;
  }
  
  
  .fb-marquee-carousel-item:hover .fb-marquee-carousel-content .fb-marquee-carousel-hover-text,
  .fb-marquee-carousel-item:hover .fb-marquee-carousel-content .fb-marquee-carousel-text {
    transform: translateY(-100%);
  }
  
  
  
// marquee carousel overlay
  
  .fb-marquee-carousel-container {
    position: relative;
    overflow: hidden;
}

.fb-marquee-carousel-container::before,
.fb-marquee-carousel-container::after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    z-index: 2;
    pointer-events: none;
}

  
